import request from 'utils/request';

/**
 * 
 */
export const getblockcount =  () => {
  const opts = {
   method:'getblockcount',
   params:[],
   baseUrl:'common'
  }
  return request(opts);
}
/**
 * 
 * @param type 
 */
export const gettxcount = (type:string) => {
  const opts = {
    method:'gettxcount',
    params:[
      type
    ],
    baseUrl:'common'
  }
  return request(opts);
}
/**
 * 
 */
export const getaddrcount = () => {
  const opts = {
    method:'getaddrcount',
    params:[],
    baseUrl:'common'
  }
  return request(opts);
}
/**
 * 
 * @param size 
 * @param page 
 */
export const getblocks = (size:number,page:number) => {
  const opts = {
    method:'getblocks',
    params:[
      size,
      page
    ],
    baseUrl:'common'
  }
  return request(opts);
}

/**
 * 
 * @param size 
 * @param page 
 * @param type 
 */
export const getrawtransactions = (size:number,page:number,type:string) => {
  const opts = {
    method:'getrawtransactions',
    params:[
      size,
      page,
      type
    ],
    baseUrl:'common'
  } 
  return request(opts);
}
/**
 * 
 * @param str 
 */
export const searchSomething = (str:string)=>{
  const opts = {
    method:'fuzzysearchasset',
    params:[
      str
    ]
  }
  return request(opts)
}


