import request from 'utils/request';

/**
 * 
 * @param addr 
 */
export const getaddrinfo = (addr: string) =>
{
    const opts = {
        method: 'getaddr',
        params: [
            addr
        ],
        baseUrl: 'common'
    }
    return request(opts);
}
/**
 * 
 * @param addr 
 */
export const getaddrbalance = (addr: string) =>
{
    const opts = {
        method: 'getbalance',
        params: [
            addr
        ],
        baseUrl: 'common'
    }
    return request(opts);
}
/**
 * nep5
 * @param nep5 nep5
 */
export const getaddrnep5asset = (nep5: string) =>
{
    const opts = {
        method: 'getallnep5assetofaddress',
        params: [
            nep5,
            1
        ],
        baseUrl: 'common'
    }
    return request(opts);
}

/**
 * 
 * @param addr 
 * @param size 
 * @param page 
 */
export const getaddrtxlist = (addr: string, size: number, page: number) =>
{
    const opts = {
        method: 'getaddresstxs',
        params: [
            addr,
            size,
            page
        ]
    }
    return request(opts);
}
/**
 * utxo
 * @param addr 
 * @param size 
 * @param page 
 */
export const getaddrutxolist = (addr: string, page: number, size: number) =>
{
    const opts = {
        method: 'getutxolistbyaddress',
        params: [
            addr,
            page,
            size
        ]
    }
    return request(opts);
}


