import request from 'utils/request';

/**
 * 
 */
export const getaddrcount = () => {
  const opts = {
    method:'getaddrcount',
    params:[],
    baseUrl:'common'
  }
  return request(opts);
}
/**
 * 
 * @param size 
 * @param page 
 */
export const getaddrlist = (size:number,page:number) => {
  const opts = {
    method:'getaddrs',
    params:[
      size,
      page
    ],
    baseUrl:'common'
  }
  return request(opts);
}


