import request from 'utils/request';
/**
 * 
 * @param assetid id
 */
export const getassetinfo = (assetid: string) =>
{
    const opts = {
        method: 'getasset',
        params: [
            assetid
        ],
        baseUrl: 'common'
    }
    return request(opts);
}
/**
 * 
 * @param nep5id id
 */
export const getnep5info = (nep5id: string) =>
{
    const opts = {
        method: 'getnep5asset',
        params: [
            nep5id
        ],
        baseUrl: 'common'
    }
    return request(opts);
}
/**
 * 
 * @param assetid id
 */
export const getrankbyassetcount = (assetid: string) =>
{
    const opts = {
        method: 'getrankbyassetcount',
        params: [
            assetid
        ]
    }
    return request(opts);
}
/**
 * 
 * @param assetid id
 * @param size 
 * @param page 
 */
export const getrankbyasset = (assetid: string, size:number,page:number) =>
{
    const opts = {
        method: 'getrankbyasset',
        params: [
            assetid,
            size,
            page
        ]
    }
    return request(opts);
}

/**
 * 
 * @param type 
 * @param nep5id nep5id
 */
export const getnep5count = (type:string,nep5id: string) =>
{
    const opts = {
        method: 'getnep5count',
        params: [
            type,
            nep5id
        ],
        baseUrl: 'common'
    }
    return request(opts);
}

/**
 * nep5
 * @param nep5id id
 * @param size 
 * @param page 
 */
export const getnep5transfersbyasset = (nep5id: string, size:number,page:number) =>
{
    const opts = {
        method: 'getnep5transfersbyasset',
        params: [
            nep5id,
            size,
            page
        ],
        baseUrl: 'common'
    }
    return request(opts);
}


