/**
 * 
 * @param size 
 * @param page 
 * @param type 
 */
export const gettransactionlist = (page: number, size: number, type: string) =>
{
  const opts = {
    method: 'gettransactionlist',
    params: [
      page,
      size,
      type
    ]
  }
  return request(opts);
}

/**
 * 
 * @param txid id
 */
export const gettraninfo = (txid: string) =>
{
  const opts = {
    method: 'getutxoinfo',
    params: [
      txid
    ]
  }
  return request(opts);
}
/**
 * txidnep5
 * @param txid id
 */
export const getnep5transferbytxid = (txid:string) => {
  const opts = {
    method:'getnep5transferbytxid',
    params:[
      txid
    ],
    baseUrl:'common'
  }
  return request(opts);
}
/**
 * nep5id
 * @param nep5 
 */
export const getnep5asset = (nep5:string) => {
  const opts = {
    method:'getnep5asset',
    params:[
      nep5
    ],
    baseUrl:'common'
  }
  return request(opts);
}


