import request from 'utils/request';

/**
 * asset
 */
export const getallasset = () =>
{
    const opts = {
        method: 'getallasset',
        params: [],
        baseUrl: 'common'
    }
    return request(opts);
}
/**
 * nep5
 */
export const getallnep5 = () =>
{
    const opts = {
        method: 'getallnep5asset',
        params: [],
        baseUrl: 'common'
    }
    return request(opts);
}





