import request from 'utils/request';

/**
 * 
 */
export const getblockcount =  () => {
  const opts = {
   method:'getblockcount',
   params:[],
   baseUrl:'common'
  }
  return request(opts);
}

/**
 * 
 * @param size 
 * @param page 
 */
export const getblocks = (size:number,page:number) => {
  const opts = {
    method:'getblocks',
    params:[
      size,
      page
    ],
    baseUrl:'common'
  }
  return request(opts);
}

/**
 * 
 * @param index 
 */
export const getblock = (index:number) => {
  const opts = {
    method:'getblock',
    params:[
      index
    ],
    baseUrl:'common'
  }
  return request(opts);
}


